.grid {
    --gutter: 0.25em;
    -ms-flex: 0 1 auto;
        flex: 0 1 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin: var(--gutter);
}

.grid__item {
    --gutter: 0.25em;
    -ms-flex: 0 1 auto;
        flex: 0 1 auto;
    margin: var(--gutter);
    width: calc(25% - var(--gutter) * 2);
    height: 10em;
    display: -ms-flexbox;
    display: flex;
}

.image {
    -ms-flex: 1 1;
        flex: 1 1;
    display: inherit;
    width: 100%;
    height: 100%;
    max-width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}

@media (max-width: 50em) {
    .grid__item {
        width: calc(33% - var(--gutter) * 2);
    }
}

@media (max-width: 30em) {
    .grid__item {
        width: calc(100% - var(--gutter) * 2);
    }
}



