.loader {
    position: absolute;
    top: 0;
    width: 100%;
    height: 5px;
    background-color: rgba(225, 225, 225, 0.5);
    overflow: hidden;
}

.loader__container {
    width: 100%;
    -webkit-animation: load 2s linear infinite;
            animation: load 2s linear infinite;
}

.loader__bar {
    height: 6px;
    background-color: var(--color-accent);
    position: absolute;
    -webkit-animation: squish 1.75s linear alternate infinite;
            animation: squish 1.75s linear alternate infinite;
}

@-webkit-keyframes load {
    from {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    to {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}

@keyframes load {
    from {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    to {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}

@-webkit-keyframes squish {
    0%,
    100% {
        width: 25%;
    }
    50% {
        width: 60%;
    }
}

@keyframes squish {
    0%,
    100% {
        width: 25%;
    }
    50% {
        width: 60%;
    }
}



